body {
  margin: 0;
  padding: 0;
  font-family:"Courier New", Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left, rgb(115, 121, 201), rgb(219, 129, 147));
  background: linear-gradient(to right, rgb(115, 121, 201), rgb(219, 129, 147));
}

a, a:focus, button, button:focus {
  cursor: pointer;
  outline:none;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.particles {
  position: fixed;
  top:0;
  left:0;
  bottom:0;
  right:0;
  z-index: -1;
}
* { 
    box-sizing: border-box; 
  }
  
  table {
    border-collapse: separate;
    background:#fff;
   border-radius: 5px;
    margin:10px auto;
    box-shadow:(0px 0px 5px rgba(0,0,0,0.3));
    text-align: center;
  }
  
  thead {
    border-radius:5px;
  }

  thead th {
    
    font-size:16px;
    font-weight:400;
    color:#fff;
    text-shadow:(1px 1px 0px rgba(0,0,0,0.5));
    text-align:center;
    padding:10px;
    background-image:-webkit-linear-gradient(#646f7f, #4a5564);
    background-image:linear-gradient(#646f7f, #4a5564);
    border-top:1px solid #858d99;
    }
    thead th:first-child {
    border-top-left-radius:5px; 
    }
  
    thead th:last-child {
    border-top-right-radius:5px; 
    }
  
  tbody tr td {
    font-family: 'Open Sans', sans-serif;
    font-weight:400;
    color:#5f6062;
    font-size:13px;
    padding:20px 20px 20px 20px;
    border-bottom:1px solid #e0e0e0;
    
  }
  tbody tr:first-child td:first-child{
    color:gold;
    font-weight: bold;
  }
  tbody tr:nth-child(2) td:first-child{
    color:silver;
    font-weight: bold;
  }
  tbody tr:nth-child(3) td:first-child{
    color:brown;
    font-weight: bold;
  }
  
  tbody tr:nth-child(2n) {
    background:#f0f0f0;
  }
  
  tbody tr:last-child td {
    border-bottom:none;
  }
  tbody tr:first-child {
     border-bottom-left-radius:5px;
    }
    tbody tr:last-child {
    border-bottom-right-radius:5px;
    }
@-webkit-keyframes scale-up {
  0% {-webkit-transform: scale(0);transform: scale(0)}
  50% {-webkit-transform: scale(1.08);transform: scale(1.08)  }
  100% {-webkit-transform: scale(1);transform: scale(1) }
}
@keyframes scale-up {
  0% {-webkit-transform: scale(0);transform: scale(0)}
  50% {-webkit-transform: scale(1.08);transform: scale(1.08)  }
  100% {-webkit-transform: scale(1);transform: scale(1) }
}
.Tilt {
    display:inline-block;
}
.Tilt-inner {
    background: -webkit-linear-gradient(left, rgb(38, 44, 136),rgb(78, 78, 78));
    background: linear-gradient(to right, rgb(38, 44, 136),rgb(78, 78, 78));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
}
.boxColor {
    background-image:
    -webkit-linear-gradient(325deg, rgba(206, 190, 41, .3) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .3) 71%, rgba(137, 180, 255, .3) 95%),
    -webkit-linear-gradient(178deg, rgba(206, 190, 41, .8) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .7) 71%, rgba(137, 180, 255, .7) 95%),
    -webkit-linear-gradient(67deg, rgba(206, 190, 41, 1) 0%, rgba(155, 31, 80, .6) 33%, rgba(41, 137, 216, 1) 71%, rgba(137, 180, 255, 1) 95%);
    background-image:
    linear-gradient(125deg, rgba(206, 190, 41, .3) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .3) 71%, rgba(137, 180, 255, .3) 95%),
    linear-gradient(-88deg, rgba(206, 190, 41, .8) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .7) 71%, rgba(137, 180, 255, .7) 95%),
    linear-gradient(23deg, rgba(206, 190, 41, 1) 0%, rgba(155, 31, 80, .6) 33%, rgba(41, 137, 216, 1) 71%, rgba(137, 180, 255, 1) 95%);
}

.imageContainer {
    display: flex;
    justify-content: center;
    margin: 20px 0;
}
@media (max-width:1024px) {
    .imageContainer {
        display: block;
        position: absolute;
        left:0;
        right:0;
        margin:0;
    }
}
.currentImage {
    width:auto;
    height:500px;
    opacity:0;
}
.visible {
    opacity:1;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
}

  .loaderSmall {
    display: inline-block;
    color: #ffffff;
    font-size: 3px;
    width: 1em;
    height: 1em;
    top:50%;
    right:-50%;
    border-radius: 50%;
    position: absolute;
    text-indent: -9999em;
    -webkit-animation: load4 1.3s infinite linear;
    animation: load4 1.3s infinite linear;
    -webkit-transform: translateZ(0);
    transform: translateZ(0) translate(0,-50%);
  }
  .loaderBig {
    color: #ffffff;
    font-size: 20px;
    margin: 100px auto;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    text-indent: -9999em;
    -webkit-animation: load4 1.3s infinite linear;
    animation: load4 1.3s infinite linear;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }

  @-webkit-keyframes load4 {
    0%,
    100% {
      box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
    }
    12.5% {
      box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    25% {
      box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    37.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    50% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    62.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
    }
    75% {
      box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
    }
    87.5% {
      box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
    }
  }

  @keyframes load4 {
    0%,
    100% {
      box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
    }
    12.5% {
      box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    25% {
      box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
    }
    37.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    50% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
    }
    62.5% {
      box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
    }
    75% {
      box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
    }
    87.5% {
      box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
    }
  }

@media (max-width: 600px) {
    .currentImage {
        width:100%;
        height:auto;
        margin:0;
        padding:0;
    }
}
.info {
    margin:0;
    background: rgba(255, 255, 255, 0.2);
    height:0;
    -webkit-animation:height 1s forwards;
            animation:height 1s forwards;
    padding:10px;
    overflow: hidden;
}
@-webkit-keyframes height {
    100% {height:100%}
}
@keyframes height {
    100% {height:100%}
}
.info:hover {
    background: rgba(255, 255, 255, 0.603);
}
.info-paragraph {
    display: flex;
    justify-content: space-between;
    padding:2px;
    margin:0;
}

.detectedName {
    color:rgb(3, 4, 109);
    
}
.detectedValue {
    padding-left:2px;
}

@media (max-width:1024px) {
    .info {
        position: absolute;
        right:0;
        top:0; 
    }
    }
    @media (max-width:600px) {
        .info {
            margin:0;
            position:static;
            width:100%;
            height:auto;
            background: rgba(255, 255, 255, 0.603);
        }
    }
.main {
    padding:40px;
    border:1px solid rgba(100,100,100,0.6);
    border-radius:8px;
}
